@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  padding: 15px;
  font-family: 'Josefin Sans', cursive;
  font-size: 20px;
}

.main-header {
font-size: 2rem;
position: relative;
height: 25rem;
background-repeat: no-repeat;
background-position: bottom;
background-size: cover;
background-image: -webkit-linear-gradient(top left, rgb(228, 187, 99), rgba(172, 69, 0, 0.5)),url("https://www.mcdonalds.com/content/dam/usa/promotions/HP_hero/hh-bts-d.jpg");
background-image: linear-gradient(to bottom right, rgb(228, 187, 99), rgba(172, 69, 0, 0.5)),url("https://www.mcdonalds.com/content/dam/usa/promotions/HP_hero/hh-bts-d.jpg");
-webkit-clip-path: polygon(0 0, 100% 0%, 100% 70%, 0% 100%);
        clip-path: polygon(0 0, 100% 0%, 100% 70%, 0% 100%);
}


.logo {
  display: block;
  font-size: 30px;
  padding: 15px;
}


.main-nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  justify-content: space-between;
  align-items: center;
}
.main-nav ul a {
  text-decoration: none;
  color: white;
}
.main-nav li:last-child {
  padding-right: 20%;
}

.icon{
  height: 2rem;
  width: 2rem;
  display: flex;
  flex-direction: column;
  padding-bottom: 4px;
}

.main-icon {
  position: absolute;
  top: 30%;
  left: 1%;
  background: none;
  outline: none;
  cursor: pointer;
}


.icon span::after {
  content: "";
  height: 0.5rem;
 
}

.main-icon{
display: flex;
border: none;
border-radius: 5px;
}

.icon span {
  border-bottom: 3px solid #ccc;
  display: flex;
  flex-direction: column;
  border-radius: 4px;
}
.main-form{
  margin-top: 5%;
  text-align: center;
}

.main-form .main-input{
  font-family: 'Josefin Sans', cursive;
  text-align: center;
  font-size: 120%;
  width: 35vw;
  height: 5vh;
  border-radius: 5px;
  border: none;
  border-bottom: 1px solid #ccc;
  outline: none;
  position: relative;
  display: inline-block;
 
}


.main-form .main-btn {
  border: none;
  height: 4.5vh;
  width: 3vw;
  border-radius: 100px;
  background-color: transparent;
  border: 1px solid #ccc;
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
}

.main-form .main-btn:hover {
  background: -webkit-linear-gradient(top left, rgb(228, 187, 99), rgba(172, 69, 0, 0.5));
  background: linear-gradient(to bottom right, rgb(228, 187, 99), rgba(172, 69, 0, 0.5));
  color: white;
}


/* MAIN ITEMS STYLES */

.container-box {
  margin-top:6rem;
  display: flex;
 flex-flow: row wrap;

}

.recipe-box{
  display: flex;
  margin: 2rem auto;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 2px 2px 10px #ccc;
  height: 20rem;
  width: 20rem;
  background-color: #ccc;
  border-radius: 20px;
  color: white;
  text-align: center;
  justify-content: space-around;
  flex-direction: column;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  position: relative;
  -webkit-animation: boxShower 1s ease-in;
          animation: boxShower 1s ease-in;
  font-size: 150%;

}

@-webkit-keyframes boxShower{
  0% {
    opacity: 0;
    -webkit-transform: translateY(100%);
            transform: translateY(100%)
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0)
  }
}

@keyframes boxShower{
  0% {
    opacity: 0;
    -webkit-transform: translateY(100%);
            transform: translateY(100%)
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0)
  }
}

/* animation for the boxes */

.recipe-box > h4 {
flex-basis: 1;
align-self:center;

}

.recipe-box p {
  flex-basis: 0;
  align-self:center;
}

.flip {
  display: none;
}

.backside {
-webkit-transform: rotateY(180deg);
        transform: rotateY(180deg);
position: absolute;
 display: none;
 height: 100%;
 width: 100%;
 background: -webkit-linear-gradient(left bottom, rgb(228, 187, 99), #ccc);
 background: linear-gradient(to right top, rgb(228, 187, 99), #ccc); 
 border-radius: 20px;
}


.backside a, .like-btn {
font-size: 1rem;
color: black;
text-decoration: none;
border: 1px solid white;
padding: 1.2rem;
border-radius: 10px;
background-color: white;
cursor: pointer;
height: 26%;
margin-top:25%;
margin-right:1%;
font-weight: 900;
-webkit-transition: all .2s ease;
transition: all .2s ease;
}

.backside a:hover, .like-btn:hover {
  color: rgb(231, 172, 45);
  border: 1px solid rgb(231, 172, 45);
}



.recipe-box:hover .backside{
 display: block;
 padding-top: 20%;
 
}

.recipe-box:hover {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}


/* USER PICK RECIPE */
.container-active {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
 
}
.container-active h4 {
margin-bottom: 5%;
}

.active-pick {
  background-repeat: no-repeat;
  background-size: cover;
  height: 30rem;
  width: 30rem;
  justify-content: center;
  
}
